.login{width:100%;display:-ms-flexbox;display:flex}.login__head{color:#1b2b35;font-weight:bold;padding-bottom:60px;opacity:0.7}.login__header{font-size:1.6rem;margin-bottom:-2px}.login__subheader{font-size:1.1rem}.login__section{width:50%;-webkit-box-sizing:border-box;box-sizing:border-box;padding:35px 45px}.login__section--login{background-color:white;-webkit-animation:login__section--login 1s cubic-bezier(0.4, 0, 0.2, 1) backwards;animation:login__section--login 1s cubic-bezier(0.4, 0, 0.2, 1) backwards}@-webkit-keyframes login__section--login{from{-webkit-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0)}to{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes login__section--login{from{-webkit-transform:translate3d(-100%, 0, 0);transform:translate3d(-100%, 0, 0)}to{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}.login__section--register{color:#eee;background-color:#1b2b35;padding-top:60px;-webkit-animation:login__section--register 1s cubic-bezier(0.4, 0, 0.2, 1) backwards;animation:login__section--register 1s cubic-bezier(0.4, 0, 0.2, 1) backwards}@-webkit-keyframes login__section--register{from{-webkit-transform:translate3d(-50%, 0, 0);transform:translate3d(-50%, 0, 0);opacity:0}to{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);opacity:1}}@keyframes login__section--register{from{-webkit-transform:translate3d(-50%, 0, 0);transform:translate3d(-50%, 0, 0);opacity:0}to{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);opacity:1}}.login__wrapper--register{padding-top:40px}.login__form{padding:10px 0 15px}.login__form-header{display:inline;font-size:1.8rem;padding:0 30px 5px 0;border-bottom:5px solid #00f7e1}.login__item{width:80%;max-width:300px;font-size:1.1rem;margin-top:15px;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column}.login__input{padding:9px;border-radius:2px;background-color:#eee;border:none}.login__label{font-weight:bold;padding-bottom:4px}.login__button{background-color:#00f7e1;color:#212121;border-radius:4px;border:none;margin-top:15px;font-weight:bold;padding:12px 30px}@media (max-width: 768px){.login{-ms-flex-direction:column;flex-direction:column}.login__head{padding-bottom:30px}.login__section{width:100%}}@media (max-width: 425px){.login__section{min-height:100vh}.login__item{width:100%}}



